<template>
  <div id="room-preview">
    <div class="room-options">
      <input type="file" class="upload-banner-image" id="uploadInput" @change="readURL">
      <select class="select-size" v-model="selectedSize">
        <option altvalue="60" value="31.5">3"x3"</option>
        <option altvalue="45" value="41.5">4"x4"</option>
        <option altvalue="30" value="63">6"x6"</option>
        <option altvalue="25" value="83">8"x8"</option>
        <option altvalue="15" value="125">12"x12"</option>
        <option altvalue="7.8" value="250">24"x24"</option>
      </select>
      <br>
    </div>
    <div class="room-layers">
      <span id="cloud-layers" v-html='cloud'></span>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import cloudinary from 'cloudinary';

export default {
  data: function() {
    return {
      selectedSize: 250,
      backgroundImage: 'test:eye_balls'
    };
  },
  methods: {
    readURL() {
      let input = document.getElementById('uploadInput');
      console.log(input);
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          cloudinary.v2.uploader.unsigned_upload(
            e.target.result,
            'f05u9z4r',
            { folder: 'test/' },
            (error, result) => {
              let original = result.public_id;
              this.backgroundImage = original.replace('/', ':'); // Cloudinary urls require : instead of / for folders
            }
          );
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
    // TODO: Implement cloudinary upload widget?
    openWidget() {
      cloudinary.openUploadWidget(
        { cloud_name: 'rfathead', upload_preset: 'f05u9z4r' },
        (error, result) => {
          console.log(error, result);
        }
      );
    }
  },
  computed: {
    cloud() {
      return cloudinary.image('test/wall', {
        transformation: [
          {
            overlay: this.backgroundImage,
            flags: 'tiled',
            opacity: 85,
            effect: 'multiply',
            width: this.selectedSize
          },
          {
            effect: 'blur:10'
          },
          {
            overlay: 'test:furniture'
          },
          {
            overlay: 'test:noise',
            effect: 'multiply'
          },
          {
            overlay: 'test:shadows',
            effect: 'multiply'
          },
          {
            overlay: 'test:lighting',
            effect: 'multiply'
          }
        ]
      });
    }
  }
};
</script>
<style <style lang="scss" scoped>

</style>



